* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

input {
  height: 40px;
  font-size: 20px;
  display: block;
  text-align: center;
  margin-left: 40px;
  margin-bottom: 20px;
}

button[data-start]{
  height: 40px;
  width: 250px;
  margin-left: 40px;
  font-size: 20px;
}


.timer {
    display: flex;
    flex-direction: row;
}

.field:not(:first-child ) {
  margin-left: 20px;
}
.field {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.value {
font-size: 40px;
font-weight: 500
}

.label {
  font-size: 20px;
  font-weight: 500;
  text-transform: uppercase;
}

